<template>
    <div class="container">
    <!--  PDF  -->
    <div class="sidebar">
      <h2></h2>
      <PdfList
      v-for="(item, index) in Obj"
      :key="index"
      :Obj="item"
      :isChooseMode="isChooseMode"
      @displayit="choosePdf(item)"
    />
    </div>

    <!--  PDF  -->
    <div class="pdf-viewer">
      <h2>PDF</h2>
      <div class="display-place">
        <div ref="pdfContainer" class="pdf-container">
          <canvas v-for="(page, index) in pages" :key="index" :ref="'pdfCanvas' + index" class="pdf-canvas"></canvas>
        </div>
      </div>
      <!-- <iframe v-if="currentPdfUrl" :src="currentPdfUrl" frameborder="0"></iframe> -->
    </div>

    <!--  RAG  -->
    <div class="rag-qa">
      <h2>RAG </h2>
      <div class="DialogBox">
        <div v-for="(dialog, index) in dialogList" :key="index" class="dialog-item">
          <p><strong></strong>{{ dialog.question }}</p>
          <p><strong>AI</strong>{{ dialog.answer }}</p>
        </div>
      </div>
      <div class="InputText">
        <input
          type="text"
          maxlength="500"
          v-model="inputValue"
          placeholder="..."
        />
        <img src="../assets/send.png" class="submit-btn"/>
      </div>
    </div>

  </div>
</template>
  
<script>
  import PdfList from '@/components/PdfList.vue'
  import axios from 'axios'
  import { getDocument } from 'pdfjs-dist/build/pdf';
  import { GlobalWorkerOptions } from 'pdfjs-dist/build/pdf';
  export default {
    name: 'SaveDirection',
    components: {
      PdfList
    },
  
    data() {
      return {
        pdfFiles: [ // 
        { name: 'PDF1', url: '/path/to/pdf1.pdf' },
        { name: 'PDF2', url: '/path/to/pdf2.pdf' },
        { name: 'PDF3', url: '/path/to/pdf3.pdf' }
      ],
      currentPdfUrl: '', //  PDF URL
      question: '', // 
      answer: '', // 
      dialogList: [], // 
      Obj: [],
      diaplayId: 0,
      pages:[]
      }
    },
  
    // computedmethods
    methods: {
      
      getList() {
        let list = [];
        let newObjects = {};

        axios.get('http://localhost:8087/getCollectArticles')
          .then(res => {
            list = res.data;

            for (let i = 0; i < list.length; i++) {
              newObjects[i] = list[i];
            }
            this.Obj = newObjects;
        })
        .catch(error => {
          this.$message({
            message: '',
            type: 'error',
            duration: 2000
          });
          console.error("", error);
        });
      },
      showPdf(url) {
        this.currentPdfUrl = url;
      },
      askQuestion() {
        //  RAG API 
        //  API 
      this.answer = `${this.question}...`;
      },
      async choosePdf(item){
        this.displayId=item.id;
        console.log(item.id);

        try {
            const response = await axios.get('http://localhost:8087/getPdfById', {
                params: { id: item.id },
                responseType: 'blob'
            });

            //  Blob 
            if (response.status === 200) {
                const url = window.URL.createObjectURL(new Blob([response.data]));
                this.currentPdfUrl = url;  //  URL  pdfUrl
                

                //  pdf.js  PDF
                const pdf = await getDocument(url).promise;  //  URL  currentPdfUrl
                const numPages = pdf.numPages; // 
                this.pages = new Array(numPages); // 
                for (let pageNum = 1; pageNum <= numPages; pageNum++) {
                    const page = await pdf.getPage(pageNum); // 
                    const scale = 1.5; // 
                    const viewport = page.getViewport({ scale });

                    //  ref  canvas
                    const pageref='pdfCanvas' + (pageNum - 1);
                    console.log(pageref);
                    const canvas = this.$refs[pageref][0]; //  refs 
                    if (canvas) { // 
                        console.log(canvas);
                        const context = canvas.getContext('2d');
                        canvas.height = viewport.height; // 
                        canvas.width = viewport.width; // 

                        const renderContext = {
                            canvasContext: context,
                            viewport: viewport
                        };
                        await page.render(renderContext).promise; // 
                    } else {
                        console.error(`Canvas for page ${pageNum} not found.`);
                    }
                }
            } else {
                console.error('Failed to fetch PDF:', response.status);
            }
        } catch (error) {
            console.error('Error loading PDF:', error);
        }
      }
    },
  
    created() {
      this.getList();
    },

    mounted(){
      GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.10.377/pdf.worker.min.js';  // CDN 
    }
  
  }
</script>

<style scoped lang="scss">
.container {
  display: flex;
  flex-direction: row; /*  */
  height: 100vh;
}

.sidebar {
  width: 20%;
  padding: 10px;
  border-right: 1px solid #ccc;
  overflow-y: auto;
  position: relative;
}

.pdf-viewer {
  width: 50%;
  padding: 10px;
}

.pdf-viewer iframe {
  width: 100%;
  height: 90vh;
}

.rag-qa {
  width: 30%;
  padding: 10px;
  border-left: 1px solid #ccc;
  justify-content: center; /*  */
  display: flex;
  flex-direction: column;
  position: relative; /*  */
}

.DialogBox {
  flex: 1; /* DialogBox */
  overflow-y: auto; /*  */
  margin-bottom: 50px; /*  */
}

.dialog-item {
  margin-bottom: 10px;
  padding: 5px;
  background-color: #f5f5f5;
  border-radius: 5px;
}

.InputText {
  position: absolute;
  bottom: 20%;
  left: 50%; /*  */
  transform: translateX(-50%); /*  transform  */
  right: 0;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: transparent;
  width: 300px;
  z-index: 999;
}

input[type="text"] {
  flex: 1;
  height: 40px;
  padding: 0 10px;
  background-color: rgba(255, 255, 255, 0.7);
  border: 1px solid transparent;
  border-radius: 20px;
  box-shadow: 0 0 10px #42b983;
  transition: all 0.3s ease-in-out;
}

input[type="text"]:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(24, 144, 255, 0.8);
}

.submit-btn {
  height: 40px;
  padding: 2px 0px 0px 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transform: scale(1);
  transition: all 0.3s ease-in-out;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.submit-btn:hover {
  transform: scale(1.2);
}

.display-place {
    border: 1px solid #ccc; /*  */
    width: 100%; /*  */
    height: 100%; /*  */
    overflow: auto; /*  */
    position: relative; /*  */
}

.pdf-container {
    display: flex;
    flex-direction: column; /*  */
}

.pdf-canvas {
  display: block; /*  */
  width: 100%; /*  */
  height: auto; /*  */
  margin-bottom: 10px; /*  */
}
</style>